//
// Brand
//

.aside-brand {
	display: flex;
	align-items: center;
	flex-direction: row;
	padding: 0 get($page-padding, desktop);
	height: get($header-config, desktop, default, height);

	// Logo
	.aside-logo {
		display: flex;
		justify-content: flex-begin;
	}

	// Toggle Icon
	.aside-toggle {
		.svg-icon {
			svg {
				transition: $transition;
			}
		}
	}
}

.aside-users {
	display: flex;
	align-items: center;
	flex-direction: row;
	// padding: 0 get($page-padding, desktop);
	height: get($header-config, desktop, default, height);
}

// Desktop Mode
@include media-breakpoint-up(lg) {
	.aside-brand {
		// background-color: get($brand-config, base, bg-color);

		// Fixed Header & Static Aside Mode
		.header-fixed.aside-static & {
			position: fixed;
			top: 0;
			left: 0;
			width: get($aside-config, base, width);
			z-index: get($header-config, desktop, fixed, zindex);
		}

		// Fixed Header & Aside Minimize Modes
		.aside-minimize & {
			// Toggle Icon
			.aside-toggle {
				.svg-icon {
					svg {
						transition: $transition;
						transform: rotate(180deg);
						/*rtl:begin:*/
						//transform: rotate(-180deg);
						/*rtl:end:*/
					}
				}
			}
		}

		// Aside Minimize & Fixed Header & Static Aside Mode
		.header-fixed.aside-static.aside-minimize & {
			width: get($aside-config, base, width);
		}

		// Aside Minimize Toggle Icon
		.aside-toggle {
			position: absolute;
			right: 0;
			top: get($header-config, desktop, default, height) - 16px;
			height: 30px;
			width: 30px;
			border-radius: 50%;
			margin-right: -(30px/2);
			z-index: get($aside-config, base, zindex) + 1;

			i {
				transition: $transition;
			}

			// Aside Minimize Toggle Icon Active State
			.aside-minimize &,
			&.aside-toggle-active {
				i {
					transition: $transition;
					transform: rotateZ(180deg)#{'/*rtl:ignore*/'};

					[direction="rtl"] & {
						transform: rotateZ(-180deg)#{'/*rtl:ignore*/'};
					}
				}
			}
		}
	}
}

// Tablet & Mobile Modes
@include media-breakpoint-down(md) {
	.aside-brand {
		// display: none;

		// Aside Minimize Toggle Icon
		.aside-toggle {
			display: none;
		}
	}
}
