//
// Header
//
.header {
	display: flex;
	justify-content: space-between;
	height: get($header-config, desktop, default, height);
	position: relative;
	flex-shrink: 0;
	align-items: stretch;
	// overflow: auto;
	z-index: 2;
	transition: get($aside-config, base, transition);
	background-color: $white;
	border-bottom: 1px solid $gray-400;

	.container,
	.container-fluid {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
	}

	.navheader-nav.nav {
		display: flex;
		align-items: stretch;
		flex-shrink: 0;
		margin-bottom: -5px;

		.nav-item {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			// flex-basis: 0;
			transition: get($aside-config, base, transition);
			border-bottom: 5px solid transparent;
			padding: 0;
			margin: 0;

			.nav-label {
				display: flex;
				flex-grow: 1;
				flex-basis: 0;
				flex-direction: column;
				justify-content: flex-start;
				border-right: 0.5px solid transparent;
			}

			// &:first-child {
			// 	.nav-label {
			// 		padding-left: 0 !important;
			// 	}
			// }

			&:last-child {
				.nav-label {
					border-right: 0;
				}
			}
		}
		
	}
}

// Desktop Mode
@include media-breakpoint-up(lg) {
	.header {
		// Fixed Header Mode
		// &.header-fixed {
		// 	height: get($header-config, desktop, fixed, height);
		// 	position: fixed;
		// 	top: 0;
		// 	right: 0;
		// 	left: 0;
		// 	transition: get($aside-config, base, transition);
		// 	z-index: get($header-config, desktop, fixed, zindex);
		// }

		// Aside Enabled Mode
		.aside-enabled & {
			&.header-fixed {
				left: get($aside-config, base, width);
			}
		}

		// Fixed Header & Minimized Aside & Minimized Aside Hover Modes
		.header-fixed.aside-minimize-hover &,
		.header-fixed.aside-minimize & {
			left: get($aside-config, base, minimized-width);
		}

		// Fixed Header & Fixed Subheader Modes
		.header-fixed.subheader-fixed & {
			box-shadow: none !important;
		}

		// Fixed Header, Minimized Header and Aside Secondary Enabled Modes
		.header-fixed.sidebar-enabled & {
			right: get($sidebar-config, default, width);
		}

		// Fixed Aside & Fixed Subheader Modes
		.aside-fixed.header-fixed & {
			// left: get($aside-config, base, primary-width);
		}

		// Fixed Aside, Aside Secondary Panel Enabled & Fixed Subheader Modes
		.aside-fixed.aside-secondary-enabled.header-fixed & {
			// left: get($aside-config, base, primary-width) +  get($aside-config, base, secondary-width);
		}

		// Fixed Aside, Minimized Aside & Fixed Subheader Modes
		.aside-minimize.aside-fixed.header-fixed & {
			transition: get($aside-config, base, transition);
			// left: get($aside-config, base, primary-width);
		}
	}
}

@include media-breakpoint-between(lg, lg) {
	.header {
		// Fixed Header, Minimized Header and Aside Secondary Enabled Modes
		.header-fixed.sidebar-enabled & {
			right: get($sidebar-config, tablet-and-mobile, width);
		}
	}
}