//
// Aside Secondary
//

// General Mode(all devices)
.sidebar {
	width: get($sidebar-config, default, width);
	box-shadow: get($sidebar-config, default, shadow);
	background-color: get($sidebar-config, default, bg-color);
	border-left: 1px solid #EBEDF3;

	.sidebar-header {
		border-bottom: 1px solid #EBEDF3;
	}

	// Aside Scroll
	.aside-scroll {
		@include perfect-scrollbar-ver-offset(4px);
	}
}

@include media-breakpoint-between(sm, lg) {
	.sidebar {
		width: get($sidebar-config, tablet-and-mobile, width);
	}
}

// Build Aside Offcanvas Panel for mobile mode
@include offcanvas-build(sidebar, tablet-and-mobile, get($sidebar-config, offcanvas-mobile));
